<!-- 管理员 -->
<template>
  <div>
    <Row>
      <Button type="success" @click="showAdminModal()">
        <Icon type="md-add" />添加
      </Button>
      <Button type="error" class="margin-left">
        <Icon type="md-close" @click="showDelModal" />删除
      </Button>
    </Row>
    <br />
    <Table :columns="column1" :data="data1"></Table>
    <br />
    <Page :total="dataTotal" @change="page()" />

    <Modal v-model="adminModal" title="管理人员">
      <Form ref="adminForm" :model="adminForm">
        <FormItem label="姓名">
          <Input v-model="adminForm.adminName"></Input>
        </FormItem>
        <FormItem label="用户名">
          <Input v-model="adminForm.adminLoginName"></Input>
        </FormItem>
        <FormItem label="密码">
          <Input v-model="adminForm.adminPwd"></Input>
        </FormItem>
        <FormItem label="联系电话">
          <Input v-model="adminForm.adminPhone"></Input>
        </FormItem>
        <FormItem label="状态">
          <Select v-model="adminForm.userState">
            <Option :value="0" :key="0">禁用</Option>
            <Option :value="1" :key="1">启用</Option>
          </Select>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button @click="adminModal = false">取消</Button>
        <Button type="success" @click="saveAdmin">保存</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
export default {
  name: "AdminMgr",
  data() {
    return {
      adminModal: false,
      selectionIds: "",
      dataTotal: 0,
      isAdd: true,
      queryForm: {
        pageIndex: 1,
        pageSize: 10
      },
      column1: [
        {
          key: "select",
          type: "selection",
          width: 60
        },
        {
          title: "姓名",
          key: "adminName",
          minWidth: 100
        },
        {
          title: "用户名",
          key: "adminLoginName",
          minWidth: 100
        },
        {
          title: "联系电话",
          key: "adminPwd",
          minWidth: 100
        },
        {
          title: "状态",
          key: "userState",
          minWidth: 100,
          render(h, params) {
            let isBan = params.row.userState === 0;
            return h(
              "Tag",
              {
                props: {
                  color: isBan ? "default" : "success"
                }
              },
              isBan ? "禁用" : "启用"
            );
          }
        },
        {
          title: "修改",
          width: 100,
          render: (h, params) => {
            return h(
              "a",
              {
                on: {
                  click: () => {
                    this.showAdminModal(params.row);
                  }
                }
              },
              [
                h("Icon", {
                  props: {
                    type: "md-create",
                    size: 24
                  }
                })
              ]
            );
          }
        }
      ],
      data1: [],
      adminForm: {
        adminName: "",
        adminLoginName: "",
        adminPwd: "",
        adminPhone: "",
        roleId: 1,
        orgId: 1,
        userState: 0
      },
      ruleValidate: {
        adminName: [
          { required: true, message: "姓名不能为空", trigger: "blur" }
        ],
        adminLoginName: [
          { required: true, message: "用户不能为空", trigger: "blur" }
        ],
        adminPwd: [
          { required: true, message: "密码不能为空", trigger: "blur" }
        ],
        adminPhone: [
          { required: true, message: "联系电话不能为空", trigger: "blur" }
        ],
        userState: [
          {
            required: true,
            type: "number",
            message: "状态不能为空",
            trigger: "change"
          }
        ]
      }
    };
  },
  methods: {
    selectionChange(selection) {
      this.selectionIds = selection.map(item => item.adminId).join(",");
    },
    showDelModal() {
      this.$custom.showDelModal(this, () => {
        this.delPeople();
      });
    },
    showAdminModal(row = {}) {
      this.adminForm = this.$custom.handleForm(this.adminForm, row);
      this.isAdd = Object.keys(row).length === 0;
      this.adminModal = true;
    },
    page(pageIndex = 1) {
      this.queryForm.pageIndex = pageIndex;
      this.$custom.post(
        "api/admin/getAdminList",
        this.queryForm,
        this,
        data => {
          this.data1 = data.result;
          this.dataTotal = data.total || this.dataTotal;
        }
      );
    },
    saveAdmin() {
      let url = this.isAdd ? "api/admin/addAdmin" : "api/admin/updAdmin";
      this.$custom.post(url, this.adminForm, this, data => {
        this.$Message.success(data.msg);
        this.page();
        this.adminModal = false;
      });
    },
    delPeople() {
      this.$custom.post(
        "api/admin/delAdmin",
        { ids: this.selectionIds },
        this,
        data => {
          this.$Message.success(data.msg);
          this.page();
        }
      );
    }
  },
  created() {
    this.page();
  }
};
</script>


